.wp-block-blocks-course-team-members {
	display: block;

	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			/* stylelint-disable-next-line selector-class-pattern */
			> .block-editor-inner-blocks > .block-editor-block-list__layout > {

				[data-type="blocks-course/team-member"] {
					width: calc(100% / #{$i} - 20px);
				}

				.block-list-appender {
					position: relative;
					align-self: stretch;
					width: calc(100% / #{$i} - 22px);
					border: 1px dashed;
					display: flex;
					align-items: center;
					justify-content: center;
					min-height: 70px;
					margin:10px;
				}
			}
		}
	}
	/* stylelint-disable-next-line selector-class-pattern */
	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;

		[data-type="blocks-course/team-member"] {
			max-width: none;
			margin: 0;
		}

		> [data-type="blocks-course/team-member"] {
			margin: 10px;

			.block-editor-media-placeholder {
				margin-bottom:20px;
			}

			.wp-block-blocks-course-team-member-img {
				position:relative; // position the containing div wrapped around the image and spinner with a relative position, so that the spinner can have an absolute position and be placed over the image.
				margin-bottom:20px;

				&.is-loading img {
					opacity:0.3; // if the image container as the is-loading class then reduce the opacity of the img element it contains to 0.3
				}
				.components-spinner {
					position:absolute;
					left:0;
					right:0;
					top:0;
					bottom:0;
					margin:auto; // zero on all sides positions content in the center when absolute positioned with margin set to auto.
				}
			}

			h4 {
				color: #222;
				font-size: 22px;
				font-size: bold;
				margin: 0;
			}

			p {
				color: #565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px;
			}

			.wp-block-blocks-course-team-member-social-links {
				ul {
					list-style: none;
					padding:0;
					margin: 15px -5px 0;

					li {
						display: inline-block;
						margin:5px;

						span.dashicon {
							color:#4e4e4e;
						}

						&.is-selected {
							outline: 2px solid rgb(208, 40, 227);
						}

						button {
							background:none;
							border: none;
							padding:0;
							cursor: pointer;
							margin:0;
						}

						&.wp-block-blocks-course-team-member-social-add-icon-li {
							button {
								background:none;
								border: none;
								padding:0;
								cursor: pointer;
								margin:0;
								outline: 1px dashed
							}
						}
					}
				}
			}
		}
	}
}